.loader-container {
  min-height: 100vh;
  display: grid;

  .loading {
    width: 40px;
    height: 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;
    margin-top: 60px;

    span {
      width: 8px;
      background: red;
      height: 30px;

      // clip-path: polygon(
      //   65.26% 9.29%,
      //   61.37% 1px,
      //   29.81% 50.04%,
      //   60.52% 100.84%,
      //   48px 49.81%,
      //   32px 4px
      // );

      transition: background 0.1s;
      animation: colorChange 0.5s ease;
      animation-iteration-count: infinite;
      background: linear-gradient(90deg, #f75964 0%, #f99468 100%);

      &:nth-child(1) {
        animation-delay: 0.1s;
      }
      &:nth-child(2) {
        animation-delay: 0.2s;
      }
      &:nth-child(3) {
        animation-delay: 0.3s;
      }

      @keyframes colorChange {
        100% {
          background: rgba(0, 0, 0, 0.15);
        }
      }
    }
  }
}
