@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:wght@200;300;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*,
*::before,
*::after {
  margin: 0;
  font-family: "Open Sans", sans-serif;
  box-sizing: border-box;
}

html,
body {
  width: 100%;
  overflow-x: hidden;
  font-size: 16px;
}

:root {
  --background: #f4f4f4;
  --cardBg: #ffffff;
  --primary: #ffffff;
  --fontColor: #000000;
  --taskItemBorderColor: rgba(0, 0, 0, 0.15);
  --borderColor: rgba(0, 0, 0, 0.15);
  --thinBorder: rgba(0, 0, 0, 0.09);
  --hover-color: rgb(0, 81, 255);
  --colorOne: #58a3d4;
  --colorTwo: #1f6ab4;
  --focusColor: #1f1e1e;
  --iconColor: rgb(255, 0, 0);
}

.dark-mode{--colorOne: #241d1d;--colorTwo: #131b25;--borderColor: rgba(255, 255, 255, 0.1);--fontColor: #ffffff;--iconColor: rgb(255, 123, 0)}.footer{min-height:150px;display:flex;justify-content:center;align-items:center;flex-direction:column;width:100%;background:linear-gradient(90deg, var(--colorOne) 100%, var(--colorTwo) 100%);border-top:1px solid var(--borderColor);padding:0 10px}@media only screen and (min-width: 680px){.footer{padding:0 50px;width:94%}}.footer .upper{width:100%;display:flex;justify-content:space-around;align-items:center;height:120px;color:var(--fontColor);border-bottom:1px solid var(--borderColor)}@media only screen and (max-width: 680px){.footer .upper{flex-direction:column;height:300px}}.footer .upper .box{display:flex;justify-content:flex-start;align-items:center;width:100%;cursor:pointer}.footer .upper .box .icon-div .MuiSvgIcon-root{color:var(--iconColor);font-size:38px}.footer .upper .box .content{margin-left:20px}.footer .upper .box .content .heading{font-weight:800}.footer .upper .box .content .details{margin-top:6px;font-weight:200}.footer .copy-right{margin:20px;color:var(--fontColor)}

.dark-mode{--colorOne: #241d1d;--colorTwo: #131b25;--borderColor: rgba(255, 255, 255, 0.1);--focusColor: #2c3e50}.header{background:linear-gradient(190deg, var(--colorOne) 40%, var(--colorTwo) 100%);height:60px;display:flex;justify-content:space-between;padding:0 60px;padding-right:180px;align-items:center;color:white;position:fixed;top:0;width:100%;z-index:5;border-bottom:1px solid var(--borderColor)}@media only screen and (max-width: 680px){.header{flex-direction:column;height:130px;justify-content:space-around;padding:0;position:relative}}.header .main-heading{font-size:20px;text-transform:uppercase;font-weight:500;font-family:"Righteous", cursive}@media only screen and (max-width: 680px){.header .main-heading{margin:5px 0}}.header .input-field{position:relative}.header .input-field .MuiSvgIcon-root{position:absolute;left:10px;top:7px;font-size:26px;opacity:0.6}.header .input-field input{background:#43434390;color:white;outline:none;border:1px solid black;padding:7px 15px;font-size:15px;font-weight:800x;transition:all 0.3s;padding-left:40px}.header .input-field input::-webkit-input-placeholder{color:rgba(255,255,255,0.6);border:none}.header .input-field input:-ms-input-placeholder{color:rgba(255,255,255,0.6);border:none}.header .input-field input::placeholder{color:rgba(255,255,255,0.6);border:none}.header .input-field input:focus{background:var(--focusColor)}

.dark-mode{--background: #121213;--primary: #000000;--cardBg: rgb(31, 29, 29);--fontColor: #ffffff;--taskItemBorderColor: rgba(255, 255, 255, 0.1);--borderColor: rgba(255, 255, 255, 0.1);--thinBorder: rgba(255, 255, 255, 0.08);--hover-color: rgb(0, 255, 149)}.card{width:280px;min-height:350px;margin:15px 10px;background-color:var(--cardBg);box-shadow:0 0 20px 0 var(--taskItemBorderColor);border:1px solid var(--borderColor);transition:color 1s, background 0.6s;color:var(--fontColor);font-family:"IBM Plex Sans", sans-serif}.card .img{width:100%;height:180px;overflow:hidden}.card .img img{width:100%;height:100%;background-position:center;background-size:cover;transition:transform 0.9s ease;overflow:hidden}.card .img img:hover{transform:scale(1.1)}.card .title,.card .author,.card .published-at{margin:14px 20px}.card .title{font-size:15px;padding-bottom:10px;border-bottom:1px solid var(--thinBorder);font-weight:500;text-decoration:none;line-height:22px;color:var(--fontColor);-webkit-line-clamp:3;display:-webkit-box;overflow:hidden;-webkit-box-orient:vertical;height:72px}.card .title:hover{color:var(--hover-color)}.card .author{font-size:13px;font-weight:300;-webkit-line-clamp:1;display:-webkit-box;overflow:hidden;-webkit-box-orient:vertical}.card .card-footer{height:40px;display:flex;align-items:center;justify-content:space-between;padding-right:20px;border-top:1px solid var(--thinBorder)}.card .card-footer .published-at{font-size:16px;opacity:0.9}.card .card-footer .save-button{cursor:pointer;padding-top:4px}.card .card-footer .save-button.saved .MuiSvgIcon-root{color:red}

.dark-mode{--fontColor: #ffffff}.not-found{min-height:100vh;display:flex;justify-content:flex-start;align-items:center;flex-direction:column;padding-top:60px}.not-found p{color:var(--fontColor);font-size:18px}@media only screen and (max-width: 680px){.not-found p{font-size:15px}}.not-found .data-not-found{height:300px}@media only screen and (max-width: 680px){.not-found .data-not-found{height:200px}}

.dark-mode{--background: #312c38}.main{min-height:100vh;display:flex;background-color:var(--background);flex-direction:column;align-items:center;transition:background 0.4s}.main .container{width:100%}.main .container .cards{width:94%;display:flex;flex-wrap:wrap;align-items:center;justify-content:center;padding:20px;min-height:100vh}@media only screen and (max-width: 500px){.main .container .cards{width:100%}}

.loader-container{min-height:100vh;display:grid}.loader-container .loading{width:40px;height:30px;display:flex;justify-content:space-between;align-items:center;margin:0 auto;margin-top:60px}.loader-container .loading span{width:8px;background:red;height:30px;transition:background 0.1s;animation:colorChange 0.5s ease;animation-iteration-count:infinite;background:linear-gradient(90deg, #f75964 0%, #f99468 100%)}.loader-container .loading span:nth-child(1){animation-delay:0.1s}.loader-container .loading span:nth-child(2){animation-delay:0.2s}.loader-container .loading span:nth-child(3){animation-delay:0.3s}@keyframes colorChange{100%{background:rgba(0,0,0,0.15)}}

.dark-mode{--borderColor: rgba(255, 255, 255, 0.15)}.navigation-bar{background:#5d1fee;background:linear-gradient(90deg, #100d13 3%, #15141b 100%);position:fixed;left:0;top:0;width:5rem;height:100vh;transition:width 0.3s ease;color:white;box-shadow:0 0 20px 0 rgba(0,0,0,0.15);z-index:10;border-right:1px solid var(--borderColor)}.navigation-bar:hover{width:14rem}.navigation-bar:hover .expand-button span{display:block}.navigation-bar:hover .expand-button .MuiSvgIcon-root{transform:rotate(-180deg)}@media only screen and (max-width: 680px){.navigation-bar{top:auto;bottom:0;width:100%;height:3.4rem;border-top:1px solid var(--borderColor)}.navigation-bar:hover{width:100%}}.navigation-bar .expand-button{width:100%;display:flex;justify-content:center;align-items:center;padding:5px 0;background:#000;margin-bottom:20px;border-bottom:1px solid rgba(255,255,255,0.2)}@media only screen and (max-width: 680px){.navigation-bar .expand-button{display:none}}.navigation-bar .expand-button span{display:none;margin:0 10px;font-weight:500;min-width:120px;font-family:"Righteous", cursive}.navigation-bar .expand-button .MuiSvgIcon-root{transition:transform 0.4s ease;font-size:35px}.navigation-bar .links{display:flex;flex-direction:column;justify-content:center}@media only screen and (max-width: 680px){.navigation-bar .links{flex-direction:row;height:100%}}.navigation-bar .links .link-item{display:flex;align-items:center;padding:10px 0;margin:12px 0;overflow:hidden;text-decoration:none;color:#ffffff;border-bottom:1px solid rgba(255,255,255,0.15)}.navigation-bar .links .link-item.active-nav-link{position:relative}.navigation-bar .links .link-item.active-nav-link::before{position:absolute;content:"";width:100%;height:100%;background:rgba(255,255,255,0.1);border-left:3px solid #fd746c;border-top:1px solid rgba(255,255,255,0.15)}@media only screen and (max-width: 680px){.navigation-bar .links .link-item.active-nav-link::before{border-bottom:3px solid #fd746c;border-left:none;background:rgba(255,255,255,0.06)}}.navigation-bar .links .link-item:hover{cursor:pointer}@media only screen and (max-width: 680px){.navigation-bar .links .link-item{flex-direction:column;justify-content:space-around;width:100%;margin:0}}.navigation-bar .links .link-item .MuiSvgIcon-root{margin:0 24px}@media only screen and (max-width: 680px){.navigation-bar .links .link-item .MuiSvgIcon-root{margin:0;font-size:20px}}.navigation-bar .links .link-item span{margin-left:8px;min-width:200px}@media only screen and (max-width: 680px){.navigation-bar .links .link-item span{margin:0;font-size:13px;min-width:-webkit-fit-content;min-width:-moz-fit-content;min-width:fit-content}}

.home{width:100%;margin-left:5rem;margin-top:60px}@media only screen and (max-width: 680px){.home{margin-left:0;margin-bottom:3.4rem;margin-top:0}}

